<template>
	<scroll-view @scrolltolower="handleToLower" scroll-y class="recommend_view" v-if="recommends.length > 0">
		<view class="recommend_wrap">
			<view class="recommend_item" v-for="item in recommends" :key="item.id">
				<video :src="item.view_video" controls autoplay="true"></video>
			</view>
		</view>
	</scroll-view>
</template>

<script>
export default {
	data() {
		return {
			params: {
				limit: 30,
				skip: 0,
				order: 'hot'
			},
			recommends: []
		};
	},

	mounted() {
		this.getList();
	},

	methods: {
		getList() {
			this.request({
				url: 'http://157.122.54.189:9088/videoimg/v1/videowp/featured',
				data: this.params
			}).then(result => {
				console.log(result.data.res.videowp);
				this.recommends = result.data.res.videowp;
			});
		}
	}
};
</script>

